.AboutPage {
  display: grid;
  grid-template-columns: repeat(5, var(--grid-width));
  grid-template-rows: repeat(9, var(--grid-height));
  justify-items: center;
  align-items: center;
  .hi {
    font-size: 1rem;
    font-weight: 300;
    width: max-content;
    grid-area: 2 / 1 /3 /span 2;
  }

  .bohdan {
    font-size: .3rem;
    font-weight: 300;
    grid-area: 3 / 3 / 4/ span 3;
    transform: rotate(-10deg);
  }

  .play-drums {
    font-size: .4rem;
    grid-area: 5 / 3 /6 /span 3;
    transform: rotate(5deg);
  }

  .click-to-listen {
    grid-area: 6 / 3 / 7 /span 3;
    transform: rotate(15deg);
    font-size: .25rem;
  }

  .gh-link {
    font-size: 0.3rem;
    grid-area: 8 / 3 / 9 /span 3;
  }

  .gh-link a {
    color: var(--ternary-col);
    transition: color .2s ease-in-out;
  }

  .gh-link a:hover {
    color: var(--hover-col);
    cursor: pointer;
  }

  .img-wrapper {
    grid-area: 4 / 1 / span 4 /span 3;
    width: 100%;
    height: 100%;
  }
  @media (orientation:landscape) {
    .img-wrapper {
      grid-area: 4 /2/ span 4 / span 3;
    }
  }

  img {
    max-height: 100%;
    width: auto;
    padding: .15rem;
  }

  img:hover {
    cursor: pointer;
  }
}

